.loginPanel{
    width: 20%;
}

.loginPanel label{
    display: block;
}

.loginPanel input{
    display: block;
    width: 80%;
}

.loginPanel button{
    display: block;
    margin: 2% 17% 0 auto;
    padding: 10px;
    background-color: white;
    transition: .5s;
}

.loginPanel button:hover{
    background-color: rgb(38, 126, 114);
    color: yellowgreen;
}