div.app{
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    width: 90%;
    margin: 0 auto;
    /* background-color: #ddd; */
    color: #222;
}

header{
    flex-basis: 30vh;
    position: relative;
    overflow: hidden;
    /* background-color: #000;
    color: white; */
}

main{
    display: flex;
    flex-grow: 1;
    padding: 30px 0;
}

aside{
    min-width: 240px;
    /* background-color: #999; */
}

section.page{
    flex-grow: 1;
    /* background-color: red; */
}

footer{
    background-color: black;
    color: white;
    padding: 10px 0;
    text-align: center;
    text-transform: uppercase;
    margin-bottom: 50px;
}